<template>
  <div class="fl-buttons">
    <div>New feature request</div>
    <div>
      
            <b-form >
              <b-form-group id="exampleInputGroup1"
                            label="Name:"
                            label-for="exampleInput1"
                            description="">
                <b-form-input id="exampleInput1"
                              type="text"
                              v-model="name"
                              required>
                </b-form-input>
              </b-form-group>

    Enter description:
<b-form-textarea id="textarea1"
                     placeholder="Enter description:"
                     :rows="3"
                     :max-rows="6"
                     v-model="description"
                 required>
    </b-form-textarea>

            </b-form>


    </div>
    <div>
      <b-button variant="primary" size="lg" @click="handleSubmit()">Submit</b-button>
    </div>
    <verify-email ref="verifyEmail" title="Submit feature request" sitekey="6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc" action="SUBMIT" @emailVerified="handleVerifiedEmail"/>
    <b-modal ref="thankyou" id="thankyoumodal" title="Thank you" ok-only>
      <p>Your feature request has been submitted</p>
    </b-modal>

    <b-modal ref="errorMessage"
             title="Could not submit"
             header-bg-variant="danger"
             header-text-variant="light"
             ok-only
             >
      <h3>Something went wrong</h3>
    </b-modal>

  </div>
</template>

<script>
import VerifyEmail from './VerifyEmail.vue'
import gql from 'graphql-tag'

export default {
  components: {
    VerifyEmail
  },
  data: function () {
    return {
      name: 'Cool new feature',
      description: `<p>Cool new feature</p>
<p></p>
`
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('RequestFeature route confirmed')
      /* eslint-disable-next-line no-console */
      console.log(vm)

      /* eslint-disable-next-line no-undef */
      // grecaptcha.ready(function() {
      //   /* eslint-disable-next-line no-undef */
      //   grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'request_feature'});
      // })
    })
  },
  methods: {
    handleSubmit: function () {
      this.$refs.verifyEmail.showMe()      
    },
    handleVerifiedEmail: function (email) {

      if (email === '') {
        /* eslint-disable-next-line no-console */
        console.log('could not request feature: ')
        return 
      }

      this.$apollo.mutate({
        mutation: gql`mutation RequestFeature($name: String!, $description: String!) {
  requestFeature(name: $name, description: $description)
}
`,
        variables: {
          name: this.name,
          description: this.description
        }
      })
        .then(result => {
          /* eslint-disable-next-line no-console */
          console.log('RequestFeature: ' + result.data.requestFeature)

          // XXX
          if (result.data.requestFeature) {
            this.$refs.thankyou.show()
          } else {
            this.$refs.errorMessage.show()
          }
        })
        /* eslint-disable-next-line no-unused-vars */
        .catch(error => {
          this.$refs.errorMessage.show()
        })
    }
  }
}
</script>

