<template>
  <div>
    <div class="fl-center">
      <b-button-group size="lg">
        <b-button variant="success" :to="{path:'/request-feature'}">Submit feature request</b-button>
        <b-button variant="primary" :to="{path:'/trial'}">Request a trial or demo</b-button>
      </b-button-group>
    </div>
    <div class="fl-center fl-big fl-margin">
      Click on feature to see description 
    </div>
    <div class="fl-center fl-margin">
      <ul id="example-1">
        <!-- <router-link tag="li" class="fl-big" :to="{name:'want', params: { feature: 'freshdesk-integration' }}"><font-awesome-icon icon="thumbs-up" /> Freshdesk integration</router-link> -->
        <li class="fl-big fl-pointer" v-for="item in items" v-bind:key="item.message">
          <router-link tag="span" class="fl-big" :to="{name:'want', params: { feature: item.urlString }}"><font-awesome-icon :icon="makeFeatureIcon(item.urlString)" />{{ makeFeature(item.urlString) }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      items: [
        { urlString: 'avito-integration' },
        { urlString: 'foo' },
        { urlString: 'bar' }
      ]
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('Want route confirmed')
      /* eslint-disable-next-line no-console */
      console.log(vm)

      /* eslint-disable-next-line no-undef */
      grecaptcha.ready(function() {
        /* eslint-disable-next-line no-undef */
        grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'want'});
      })
    })
  },
  methods: {
    makeFeature (urlString) {
      let w = urlString.split('-')
      // capitalize 
      w[0] = w[0].charAt(0).toUpperCase() + w[0].substr(1,w[0].length)
      return w.join(' ')
    },
    makeFeatureIcon (urlString) {
      return (urlString === 'freshdesk-integration') ? 'fire' : 'thumbs-up'
    }
  }
}
</script>

<style scoped>
.fl-center {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 1em;
}
.fl-big {
  font-size: 2.5rem;
}
.fl-pointer {
  cursor: pointer;
}
.fl-margin {
  margin-top: 1cm;
}
ul{
  list-style-type:none;
}
</style>
