<template>
  <div>
    <b-button size="lg" class="my-2 my-sm-0" variant="warning" @click="linkInstagram" v-if="!hasFacebook">LINK INSTAGRAM BUSINESS ACCOUNT</b-button>
    <b-button size="lg" class="my-2 my-sm-0" variant="danger" @click="unlinkInstagram" v-if="hasFacebook">UNLINK INSTAGRAM ACCOUNT</b-button>

    <ul>
      <li v-for="(item, index) in comments" :key="index">{{item}}</li>
    </ul>
  </div>
</template>

<script>
import gql from 'graphql-tag'

export default {
  data () {
    return {
      comments: [
        'dummy comment 1',
        'dummy comment 2'
      ],
      hasFacebook: false
    }
  },
  apollo: {
    comments: {
      query: gql`{comments
}`
    },
    hasFacebook: gql`{hasFacebook
}`
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('Profile route confirmed')
      /* eslint-disable-next-line no-console */
      console.log(vm)

      /* eslint-disable-next-line no-undef */
      grecaptcha.ready(function() {
        /* eslint-disable-next-line no-undef */
        grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'profile'})
      })
    })
  },
  methods: {
    linkInstagram: function () {
      /* eslint-disable-next-line no-undef */
      FB.login(this.checkLoginState, { scope: 'email,manage_pages,pages_show_list,instagram_basic,instagram_manage_comments,public_profile' })
    },
    checkLoginState: function (response) {
      /* eslint-disable-next-line no-console */
      console.log('checkLoginState')
      /* eslint-disable-next-line no-console */
      console.log(response);

      if (response.status === 'connected') {
        /* eslint-disable-next-line no-undef */
        FB.api('/me', { fields: 'name,email' }, function(profile) {
          /* eslint-disable-next-line no-console */
          console.log('Good to see you, ' + profile.name + '.');
          
          let accessToken = response.authResponse.accessToken
          /* eslint-disable-next-line no-console */
          console.log('accessToken ' + accessToken)
        });
      } else if (response.status === 'not_authorized') {
        // the user is logged in to Facebook, 
        // but has not authenticated your app
      } else {
        // the user isn't logged in to Facebook.
      }
    },
    unlinkInstagram: function () {
      /* eslint-disable-next-line no-console */
      console.log('unlinkInstagram ')
    }
  }
}
</script>
