<template>
  <div>
    <b-form >
      <b-form-group id="exampleInputGroup1"
                    label="Email Address:"
                    label-for="exampleInput1"
                    description="">
        <b-form-input id="exampleInput1"
                      type="text"
                      v-model="email"
                      required>
        </b-form-input>
      </b-form-group>
      <b-form-group id="exampleInputGroup2"
                    label="Password:"
                    label-for="exampleInput2"
                    description="">
        <b-form-input id="exampleInput2"
                      type="text"
                      v-model="password"
                      required>
        </b-form-input>
      </b-form-group>
    </b-form>
    <b-button variant="success" v-on:click="handleSubmit()" id="loginButton">LOG IN</b-button>
    Not member yet? Create a <router-link :to="{path:'/signup'}">new account.</router-link>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      email: '',
      password: ''
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('Login route confirmed')
      /* eslint-disable-next-line no-console */
      console.log(vm)

      /* eslint-disable-next-line no-undef */
      grecaptcha.ready(function() {
        /* eslint-disable-next-line no-undef */
        grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'login'});
      })
    })
  },
  methods: {
    handleSubmit: function () {
      /* eslint-disable-next-line no-console */
      console.log('Login: handleSubmit')

      const { email, password } = this
      let thisApollo = this.$apollo
      this.$store.dispatch('AUTH_REQUEST', { email, password, thisApollo }).then(() => { // XXX
        // FIXME
        this.$router.push('/profile');
      })
    }
  }
}
</script>

<style scoped>
#howitworksdiv {
/* XXX */
  margin-top: 30cm;
}
</style>
