<template>
  <div >
    <b-modal ref="modal1"
             :title="title"
             header-bg-variant="success"
             header-text-variant="light"
             ok-only
             hide-footer
             @hidden="hidden"
             >
      <b-card no-body>
        <b-tabs card no-key-nav v-model="tabIndex">
          <b-tab no-body title="0" active>
            Please confirm you are not a robot. 
            <!-- <div class="g-recaptcha" data-sitekey="6Lces2gUAAAAAOvR2ilR1UR9KYudgrx5vi48LPNp" id="recaptchaId"></div> -->
          </b-tab>
          <b-tab no-body title="1">
            Please verify your email address
            <b-form inline>
              <label class="mr-sm-2" for="email">Email:</label>
              <b-input class="mb-2 mr-sm-2 mb-sm-0" id="email" v-model="email" />
            </b-form>
          </b-tab>
          <b-tab no-body title="2" >
            ПРОДАЙ has sent you a verification code to the email {{email}}. Please enter code now.
            <b-form inline>
              <label class="mr-sm-2" for="emailHash">Verification code:</label>
              <b-input class="mb-2 mr-sm-2 mb-sm-0" id="emailHash" v-model="emailHash" />
            </b-form>

          </b-tab>
        </b-tabs>
      </b-card>

      <!-- Control buttons-->
      <div class="text-center">
        <b-button-group class="mt-2">
          <b-button ref="next" @click="handleNext" v-bind:class="{ 'btn-secondary': nextClass, 'btn-primary': !nextClass }">{{next}}</b-button>
        </b-button-group>
        <br>
        <span class="text-muted">Step {{tabIndex}} of 2</span>
      </div>

    </b-modal>

    <b-modal ref="modal2"
             title="Could not verify email"
             header-bg-variant="danger"
             header-text-variant="light"
             ok-only
             >
      <h3>Something went wrong</h3>
    </b-modal>

  </div>
</template>

<script>
import gql from 'graphql-tag'

export default {
  name: 'verify-email',
  props: [
    'title',
    'sitekey',
    'action'
  ],
  data () {
    return {
      next: 'Next',
      nextClass: true, //'btn-secondary',
      email: '',
      tabIndex: 1,
      emailHash: '',
      token: '',
      verified: false
    }
  },  
//   apollo: {
//     verifyEmail: {
//       query: gql`query VerifyEmail($someEmail: String!) {
//   verifyEmail(email: $someEmail) 
// }
// `,
//       // Static parameters
//       variables: {
//         someEmail: 'foo'
//       }
//     }
//   },
  methods: {
    showMe: function () {
      /* eslint-disable-next-line no-console */
      // console.log('sitekey ' + this.sitekey)

      /* eslint-disable-next-line no-undef */
      // grecaptcha.render('recaptchaId', {
      //   'sitekey': this.sitekey,
      //   'callback' : this.verifyCallback,
      // })

      this.tabIndex = 1
      this.verified = false
      this.$refs.modal1.show()
    },
    handleNext () {
      switch (this.tabIndex) {
        case 0:
          /* reCAPTCHA */
          break
        case 1:
          /* eslint-disable-next-line no-console */
          console.log('tabIndex: ' + this.tabIndex)
          /* eslint-disable-next-line no-console */
          console.log('email: ' + this.email)

          this.$apollo.mutate({
            mutation: gql`mutation GetVerifyEmailToken($email: String!) {
  getVerifyEmailToken(email: $email)
}
`,
            variables: {
              email: this.email
            }
          }).
          then(result => {
            this.token = result.data.getVerifyEmailToken
            /* eslint-disable-next-line no-console */
            console.log('verify-email: token: ' + this.token)

            this.tabIndex = 2
            this.next = this.action
            /* eslint-disable-next-line no-console */
            console.log(this.$refs.next)
            // this.$refs.next.variant = 'primary' // XXX btn-secondary btn-primary
            // this.nextClass = 'btn-primary'
            this.nextClass = false
          })            
          .catch(error => {
            /* eslint-disable-next-line no-console */
            console.log('could not verify ' + error)
            this.$refs.modal2.show()
          })          

          break
        case 2:
          /* eslint-disable-next-line no-console */
          console.log('tabIndex: ' + this.tabIndex)
          /* eslint-disable-next-line no-console */
          console.log('emailHash: ' + this.emailHash)

          this.$apollo.mutate({
            mutation: gql`mutation CheckEmailCode($token: String!, $hash: String!) {
  checkEmailCode(token: $token, hash: $hash)
}
`,
            variables: {
              token: this.token,
              hash: this.emailHash
            }
          }).
          then(result => {
            this.verified = result.data.checkEmailCode
            /* eslint-disable-next-line no-console */
            console.log('verified: ' + this.verified)

            this.$refs.modal1.hide()
          })
          .catch(error => {
            /* eslint-disable-next-line no-console */
            console.log('could not check email code ' + error)
            this.$refs.modal2.show()
          })

          break
      }
    },
    hidden () { 
      /* eslint-disable-next-line no-undef */
      // grecaptcha.reset()

      this.$emit('emailVerified', (this.verified) ? this.email : '')
    },
    verifyCallback: function (response) {
      /* eslint-disable-next-line no-console */
      console.log('verifyCallback: ' + response)

      // this.nextButton = 'Next'
      this.tabIndex = 1
    }
  }
}
</script>

<style>
/* XXX */
/* reset this */
.card-footer {
  all: initial;
  * {
    all: unset;
  }
}
/* hide */
.card-footer {
  display: none;
  visibility: hidden;
}
/* reset this */
.card-header {
  all: initial;
  * {
    all: unset;
  }
}
/* hide */
.card-header {
  display: none;
  visibility: hidden;
}
/* reset this */
.card-header-tabs {
  all: initial;
  * {
    all: unset;
  }
}
/* hide */
.card-header-tabs {
  display: none;
  visibility: hidden;
}
</style>
