import Vue from 'vue'
import Vuex from 'vuex'
import gql from 'graphql-tag'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('user-token') || '',
    status: ''
  },
  getters: {
    isAuthenticated: state => {
      return !!state.token;
    }
  },
  mutations: {
    'AUTH_REQUEST': (state) => {
      state.status = 'loading'
    },
    'AUTH_SUCCESS': (state, result) => {
      state.status = 'success'
      state.token = result.data.login.token
    },
    'AUTH_LOGOUT': (state) => {
       state.token = ''
     }
  },
  actions: {
    /* eslint-disable-next-line no-unused-vars */
    'AUTH_REQUEST': ({commit, dispatch}, { email, password, thisApollo }) => { // XXX // user) => {
      return new Promise((resolve, reject) => {
        commit('AUTH_REQUEST');

        thisApollo.mutate({
          mutation: gql`mutation Login($email: String!, $password: String!) {
  login(email: $email, password: $password) {
    token
  }
}
`,
          variables: {
            email: email,
            password: password
          }
        })
          .then(result => {
            localStorage.setItem('user-token', result.data.login.token);
            commit('AUTH_SUCCESS', result)
            // TODO
            resolve(result);
          })
          .catch(err => {
            localStorage.removeItem('user-token');
            reject(err);
          })
      });
    },
    /* eslint-disable-next-line no-unused-vars */
    'AUTH_LOGOUT': ({commit, dispatch}) => {
      /* eslint-disable-next-line no-unused-vars */
      return new Promise((resolve, reject) => {
        commit('AUTH_LOGOUT')
        localStorage.removeItem('user-token')
        resolve()
      })
    }
  }
})
