import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'
import Summary from './components/Summary.vue'
import Pricing from './components/Pricing.vue'
import Login from './components/Login.vue'
import Signup from './components/Signup.vue'
import Want from './components/Want.vue'
import RequestFeature from './components/RequestFeature.vue'
import Trial from './components/Trial.vue'
import FR from './components/FR.vue'
import Privacy from './components/Privacy.vue'
import Terms from './components/Terms.vue'
import Profile from './components/Profile.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          // Summary will be rendered inside Home's <router-view>
          // when / is matched
          path: '',
          component: Summary
        },
        {
          path: 'pricing',
          component: Pricing
        },
        {
          path: 'login',
          component: Login
        },
        {
          path: 'signup',
          component: Signup
        },
        {
          path: 'want',
          component: Want
        },
        {
          path: 'request-feature',
          component: RequestFeature
        },
        {
          path: 'trial',
          component: Trial
        },
        {
          path: 'want/:feature',
          name: 'want',
          component: FR
        },
        {
          path: 'privacy',
          component: Privacy
        },
        {
          path: 'terms',
          component: Terms
        },
        {
          path: 'profile',
          component: Profile
        }
      ]
    }
  ]
})
