<template>
  <div class="home">
    <b-navbar toggleable="md" type="light" variant="light">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand :to="{path:'/'}">
        <img src="../assets/logo.png">
        Home
      </b-navbar-brand>
      <b-collapse is-nav id="nav_collapse">
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-button size="lg" class="my-2 my-sm-0" variant="link" :to="{path:'/pricing'}">Pricing</b-button>
          <b-button size="lg" class="my-2 my-sm-0" variant="outline-success" :to="{path:'/login'}" v-if="!isAuthenticated">LOG IN</b-button>
          <b-button size="lg" class="my-2 my-sm-0" variant="outline-success" @click="logout" v-if="isAuthenticated">LOG OUT</b-button>
        </b-navbar-nav>          
      </b-collapse>
    </b-navbar>
    <!-- NESTED outlet -->
    <router-view/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['isAuthenticated']),
  },
  methods: {
    logout: function () {
      this.$store.dispatch('AUTH_LOGOUT').then(() => this.$router.push('/login'))
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  height: 64px;
}
</style>
