<template>
  <div class="fl-buttons">
    <div>Request a trial or demo</div>
    <div>
      <b-button variant="primary" size="lg" @click="handleRequest()">REQUEST</b-button>
    </div>
    <verify-email ref="verifyEmail" title="Request a trial or demo" sitekey="6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc" action="REQUEST" @emailVerified="handleVerifiedEmail"/>
    <b-modal ref="thankyou" id="thankyoumodal" title="Thank you" ok-only>
      <p>Your request has been submitted. We'll get back to you shortly!</p>
    </b-modal>
    <b-modal ref="errorMessage"
             title="Could not request"
             header-bg-variant="danger"
             header-text-variant="light"
             ok-only
             >
      <h3>Something went wrong</h3>
    </b-modal>
  </div>
</template>

<script>
import VerifyEmail from './VerifyEmail.vue'
import gql from 'graphql-tag'

export default {
  components: {
    VerifyEmail
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('Demo route confirmed')
      /* eslint-disable-next-line no-console */
      console.log(vm)

      /* eslint-disable-next-line no-undef */
      grecaptcha.ready(function() {
        /* eslint-disable-next-line no-undef */
        grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'trial'});
      })
    })
  },
  methods: {
    handleRequest: function () {
      this.$refs.verifyEmail.showMe()
    },
    handleVerifiedEmail: function (email) {
      if (email === '') {
        this.$refs.errorMessage.show()
        return 
      }

      this.$apollo.mutate({
        mutation: gql`mutation RequestTrial($email: String!) {
  requestTrial(email: $email)
}
`,
        variables: {
          email: email
        }
      })
        .then(result => {
          /* eslint-disable-next-line no-console */
          console.log('Trial: ' + result.data.requestTrial)

          // XXX
          if (result.data.requestTrial) {
            this.$refs.thankyou.show()
          } else {
            this.$refs.errorMessage.show()
          }
        })
        /* eslint-disable-next-line no-unused-vars */
        .catch(error => {
          this.$refs.errorMessage.show()
        })
    },

  }
}
</script>

