<template>
  <div>
    <b-jumbotron>
      <template slot="header">
        {{ header }}
      </template>
      <template slot="lead">
        <div v-html="lead"></div>
      </template>
      <hr class="my-4">
      <!-- <p> -->
      <!--   Blah blah blah -->
      <!-- </p> -->
    </b-jumbotron>
    <b-button variant="primary" v-on:click="handleUpvote()">UPVOTE</b-button>
    <div>Would you like to see what others want? Go to <router-link :to="{path:'/want'}">all feature requests.</router-link></div>
    <verify-email ref="verifyEmail" title="Upvote" sitekey="6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc" action="UPVOTE" @emailVerified="handleVerifiedEmail"/>
    <b-modal ref="thankyou" id="thankyoumodal" title="Thank you" ok-only>
      <p>Your feedback has been submitted</p>
    </b-modal>
    <b-modal ref="errorMessage"
             title="Could not upvote"
             header-bg-variant="danger"
             header-text-variant="light"
             ok-only
             >
      <h3>Something went wrong</h3>
    </b-modal>
  </div>
</template>

<script>
import VerifyEmail from './VerifyEmail.vue'
import gql from 'graphql-tag'

export default {
  components: {
    VerifyEmail
  },
  data: function () {
    return {
      features: [
        {name: 'avito-integration', description: `<div><p>It would great to have it integrated Avito</p>
<p>Blah blah</p>
<ul>
<li>1 Blah</li>
<li>2 Blah</li>
</div>`},
        {name: 'foo', description: 'Foo Blah blah'},
        {name: 'bar', description: 'Bar Blah blah'},
      ],
      show: {
        // verifyEmail: false
      }
    }
  },
  computed: {
    feature: function () {
      return this.features.find( f => f.name === this.$route.params.feature ); 
    },
    header: function () {
      return this.makeFeature(this.feature.name)
    },
    lead: function () {
      return this.feature['description']
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      let urlString = vm.$route.params.feature
      // access to component instance via `vm`
      /* eslint-disable-next-line no-console */
      console.log('FR ' + urlString + ' route confirmed')

      /* eslint-disable-next-line no-undef */
      grecaptcha.ready(function() {
        /* eslint-disable-next-line no-undef */
        grecaptcha.execute('6LdtvGgUAAAAAIxuc0NTMnu1UER2s887s-m6U0hc', {action: 'FR_' + urlString.replace('-','_')});
      })
    })
  },
  methods: {
    handleUpvote: function () {
      /* eslint-disable-next-line no-console */
      console.log('FR: handleUpvote ' + this.$route.params.feature)

      this.$refs.verifyEmail.showMe()
    },
    handleVerifiedEmail: function (email) {
      if (email === '') {
        this.$refs.errorMessage.show()
        return 
      }

      this.$apollo.mutate({
        mutation: gql`mutation Upvote($email: String!, $featureName: String!) {
  upvote(email: $email, featureName: $featureName)
}
`,
        variables: {
          email: email,
          featureName: this.feature.name
        }
      })
        .then(result => {
          /* eslint-disable-next-line no-console */
          console.log('RequestFeature: ' + result.data.upvote)

          // XXX
          if (result.data.upvote) {
            this.$refs.thankyou.show()
          } else {
            this.$refs.errorMessage.show()
          }
        })
        /* eslint-disable-next-line no-unused-vars */
        .catch(error => {
          this.$refs.errorMessage.show()
        })
    },
    makeFeature (urlString) {
      let w = urlString.split('-')
      // capitalize 
      w[0] = w[0].charAt(0).toUpperCase() + w[0].substr(1,w[0].length)
      return w.join(' ')
    }
  }
}
</script>

<style scoped>
</style>
